.footer {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 0px;
  align-items: center;

  position: fixed;
  bottom: 0;

  padding: 1% 0;

  z-index: 1;
}

.footer p {
  color: #272727;
  text-align: center;
}